import React from "react"
import PropTypes from "prop-types"

import styles from "./empty-line.module.scss"

const EmptyLine = ({ variant }) => {
  const className =
    variant === "Paragraph" ? styles.paragraph : styles.pageContent
  return <div className={className} />
}

EmptyLine.propTypes = {
  variant: PropTypes.oneOf(["PageContent", "Paragraph"]),
}

EmptyLine.defaultProps = {
  variant: "PageContent",
}

export default EmptyLine
