import React from "react"
import PropTypes from "prop-types"
import Img from "gatsby-image"

import styles from "./work.module.scss"
import EmptyLine from "./empty-line"

const Work = props => {
  const {
    title,
    subTitle,
    children,
    mockupImage,
    colors,
    fontImage,
    designFluidImages,
    hasSecondaryBackgroundColor,
  } = props
  const headingClassName = hasSecondaryBackgroundColor
    ? styles.secondaryBackgroundColor
    : styles.primaryBackgroundColor
  return (
    <div>
      <div className={headingClassName}>
        <img src={mockupImage} alt="" className={styles.pageHeadingImage} />
      </div>
      <div className="container">
        <div className={styles.pageMainContentSection}>
          <div className={styles.pageMainContent}>
            <EmptyLine />
            <div>
              <div className={styles.projectTitle}>{title}</div>
              <div className={styles.projectSubTitle}>{subTitle}</div>
            </div>
            <EmptyLine variant="Paragraph" />
            <div className={styles.projectText}>
              {children}
              <EmptyLine variant="Paragraph" />
              <div className={styles.projectDetailsRow}>
                <div className={styles.projectDetailsLabel}>Main color:</div>
                <div>
                  {colors.map((color, colorIndex) => (
                    <span
                      key={colorIndex}
                      className={styles.projectDetailsColorBox}
                      style={{ backgroundColor: color }}
                    />
                  ))}
                </div>
              </div>
              <EmptyLine variant="Paragraph" />
              <div className={styles.projectDetailsRow}>
                <div className={styles.projectDetailsLabel}>Fonts:</div>
                <img src={fontImage} alt="" />
              </div>
              <EmptyLine variant="Paragraph" />
              <div>Full page:</div>
              <EmptyLine variant="Paragraph" />
            </div>
          </div>
        </div>
      </div>
      <div className={styles.projectImageContainer}>
        {designFluidImages.map((designFluidImg, designFluidImgIndex) => (
          <div key={designFluidImgIndex}>
            <Img fluid={designFluidImg} alt="" />
            <EmptyLine />
          </div>
        ))}
      </div>
    </div>
  )
}

Work.propTypes = {
  title: PropTypes.string.isRequired,
  subTitle: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
  colors: PropTypes.arrayOf(PropTypes.string).isRequired,
  mockupImage: PropTypes.any.isRequired,
  fontImage: PropTypes.any.isRequired,
  designFluidImages: PropTypes.arrayOf(PropTypes.any).isRequired,
  hasSecondaryBackgroundColor: PropTypes.bool,
}

Work.defaultProps = {
  hasSecondaryBackgroundColor: false,
}

export default Work
