import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import EmptyLine from "../../components/empty-line"
import mockupMicroLectorImage from "../../images/mockupMicroLector.png"
import microLectorFontsImage from "../../images/microlector-fonts.png"
import Work from "../../components/work"

export default () => {
  const { microLector } = useStaticQuery(graphql`
    query {
      microLector: file(relativePath: { eq: "microLector.png" }) {
        childImageSharp {
          fluid(maxWidth: 1200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <Work
      title="MicroLector"
      subTitle="Education Application Website"
      children={
        <React.Fragment>
          <div>
            This is the website of an education application -
            <strong> MicroLector </strong> by <strong> David Schreg </strong>
            (Switzerland). Link:&nbsp;
            <a style={{ color: "#00a28a" }} href="https://microlector.com">
              https://microlector.com
            </a>
          </div>
          <EmptyLine variant="Paragraph" />
          <div>
            The original website is still simple and basic, therefore, I create
            an updated version for this existing website. Service logos and
            details, as well as attractive information (such as expert name and
            quote, special offer), have been added.
          </div>
        </React.Fragment>
      }
      mockupImage={mockupMicroLectorImage}
      colors={["#0daeac", "#153867", "#f3e500", "#2e2b2b", "#ffffff"]}
      fontImage={microLectorFontsImage}
      designFluidImages={[microLector.childImageSharp.fluid]}
    />
  )
}
